html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  min-width: 800px;
  margin: 0;
  padding: 0;
}

body ul, body li, body nav, body header {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.nav__ul {
  list-style: none;
  display: flex;
}

.nav__li {
  margin: 0 10px;
}

.nav__li:first-child {
  margin-left: 0;
}

.nav__li:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.logo {
  height: 40px;
  margin-left: 50px;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: repeat(2, 408px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

p {
  margin: 0;
}

.images-card {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform, color, all .3s linear;
}

.card:hover {
  transform: scale(1.2);
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.reviews {
  text-align: right;
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: background-color .25s;
  display: inline-block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}
